import gql from "graphql-tag";
import { ApolloClient, ApolloQueryResult } from 'apollo-client'
import { HttpLink } from "apollo-link-http";
import { InMemoryCache } from "apollo-cache-inmemory";

import { ITodo } from './components/TodoList/TodoActions'

import fetch from 'cross-fetch';
    

export const client = new ApolloClient({
    link: new HttpLink({
        uri: "/api/graphql",
        fetch
    }),
    cache: new InMemoryCache(),
});

export const getTodos = (): Promise<ApolloQueryResult< { todos: ITodo[] }  >> => {
    return client.query({
        query: gql`
            query TodosData{
                todos {
                    _id
                    title
                    description
                    priority
                    state
                    color
                    created_date
                    is_time_limited
                    expiration_date
                }
            }
        `})
}

export const createTodo = (): Promise<ApolloQueryResult< { createTodo: Partial<ITodo> }  >> => {
    return client.query({
        query: gql`
            mutation MakeNewTodo($title: String, $description: String) {
                createTodo(title: $title, description: $description) {
                    _id
                    title
                }
            }
        `})
}
