export function componentToHex (c : number): string {
    const hex = c.toString(16)
    return hex.length === 1 ? `0${hex}` : hex;
}
// const as-1 ="asd"
export function rgbToHex(r: number, g: number, b: number) :string {
    return `#${componentToHex(r)}${componentToHex(g)}${componentToHex(b)}`
}

export function mulColor(hex:string, mul:number) : string {
    // hex to rgb
    if (hex.charAt(0) === "#") hex = hex.slice(1)
    const bigint = parseInt(hex, 16);
    // console.log(bigint)
    let r = (bigint >> 16) & 255; // tslint:disable-line
    let g = (bigint >> 8) & 255; // tslint:disable-line
    let b = bigint & 255; // tslint:disable-line

    r = Math.trunc(r*mul)
    g = Math.trunc(g*mul)
    b = Math.trunc(b*mul)

    return rgbToHex(r,g,b)
}

export const genColor = (hex:string) => [`#${hex}`, mulColor(hex,0.4)]

const gray = [ 50, 50, 50 ]
const yellow = [ 200, 100, 40 ]
const red = [ 230, 50, 50 ]

export function GetGradientColor(v: number){
    if (v > 1){ v = 1 }
    let c1
    let c2
    if (v < 0.6) {
        v = v/0.6
        c1 = gray
        c2 = yellow
    } else {
        v = (v - 0.6)/0.4
        c1 = yellow
        c2 = red
    }

    const r = c1[0] + v*(c2[0]-c1[0])
    const g = c1[1] + v*(c2[1]-c1[1])
    const b = c1[2] + v*(c2[2]-c1[2])
    return rgbToHex(r,g,b)
}

export function formatTime(s: number): string {
    if (s >= 3600) {
        return `${Math.ceil(s / 3600)}h`
    } else if (s >= 60) {
        return `${Math.ceil(s / 60)}m`
    } else if (s >= 10) {
        return `${Math.floor(s)}s`
    }
    return s.toFixed(1)
}

export function formatTime2(s : number){

    let h = Math.floor(s / 3600)
    s = s - h*3600
    if (h >= 24) h = h - 24
    const m =  Math.floor(s / 60)

    const zerofill = m.toString().padStart(2, '0').slice(-2);
    return `${h}:${zerofill}`
}

export class MiniDaemon {
    private owner: HTMLElement|null
    private rate: number
    private length: number
    // private task: (self :HTMLElement, )
    
    private static forceCall (oDmn) {
        oDmn.INDEX += oDmn.BACKW ? -1 : 1;
        if (oDmn.task.call(oDmn.owner, oDmn.INDEX, oDmn.length, oDmn.BACKW) === false || oDmn.isAtEnd()) { oDmn.pause(); return false; }
        return true;
    }

    constructor(oOwner: HTMLElement|null, fTask: ()=>boolean|void, nRate: number, nLen: number) {
        // if (!(this && this instanceof MiniDaemon)) { return; }
        if (arguments.length < 2) { throw new TypeError("MiniDaemon - not enough arguments"); }

        this.owner = null;
        this.task = null;
        this.rate = 100;
        this.length = Infinity;
        this.SESSION = -1;
        this.INDEX = 0;
        this.PAUSED = true;
        this.BACKW = true;

        if (oOwner) { this.owner = oOwner; }
        this.task = fTask;
        if (isFinite(nRate) && nRate > 0) { this.rate = Math.floor(nRate); }
        if (nLen > 0) { this.length = Math.floor(nLen); }
    }

    public pause() {
        clearInterval(this.SESSION);
        this.PAUSED = true;
    }
     
    public start(bReverse) {
        const bBackw = Boolean(bReverse);
        if (this.BACKW === bBackw && (this.isAtEnd() || !this.PAUSED)) { return; }
        this.BACKW = bBackw;
        this.PAUSED = false;
        this.synchronize();
    }

    public isActive() {
        return (!this.PAUSED)
    }
    
    public resume() {
        if (!this.isActive()) this.start()
    }
 
    private isAtEnd() {
        return this.BACKW ? isFinite(this.length) && this.INDEX < 1 : this.INDEX + 1 > this.length;
    }
     
    private synchronize() {
        if (this.PAUSED) { return; }
        clearInterval(this.SESSION);
        // this.SESSION = this.owner.setInterval(this.task, this.rate);
        this.SESSION = setInterval(MiniDaemon.forceCall, this.rate, this);
    }
}
