// }
// const todoAction = makeTag("TODO_")

export type ID = number | string

export declare interface ITodo {
    _id: ID;
    title: string
    description: string
    priority: number
    state: string
    color: string
    isEditing: boolean
    isExpanded: boolean
    created_date: number
}
export declare type ITodoState = ITodo[]



const TODO_INIT = "TODO_INIT"
const TODO_ADD = "TODO_ADD"
const TODO_TOGGLE_EXPAND = "TODO_TOGGLE_EXPAND"
const TODO_EDIT = "TODO_EDIT"
const TODO_SAVE_PENDING = "TODO_SAVE_PENDING"
const TODO_SAVE_SUCCESS = "TODO_SAVE_SUCCESS"
const TODO_SAVE_FAILED = "TODO_SAVE_FAILED"
const TODO_DELETE = "TODO_DELETE"

export const todoAdd = (title: string) => 
    ({ type: TODO_ADD, title })

export const todoExpand = (_id: ID) => 
    ({ type: TODO_TOGGLE_EXPAND, _id })

export const todoEdit = (_id: ID) => 
    ({ type: TODO_EDIT, _id })

export const todoDeleteClient = (_id: ID) => 
    ({ type: TODO_DELETE, _id })

export const todoSavePending = (_id: ID) => 
    ({ type: TODO_SAVE_PENDING, _id })    
export const todoSaveSuccess = (_id: ID, serverData: Partial<ITodo>) => 
    ({ type: TODO_SAVE_SUCCESS, _id, serverData })
export const todoSaveFailed = (_id: ID) => 
    ({ type: TODO_SAVE_FAILED, _id })


export const todoReducer = (state: ITodoState = [], action: AnyAction ): ITodoState => {
    switch (action.type) {
        case TODO_INIT: {
            return action.newState
        }
        case TODO_TOGGLE_EXPAND: {
            return state.map(todo => {
                if (todo._id === action._id && !todo.isEditing )
                    return {
                        ...todo,
                        isExpanded: !todo.isExpanded,
                    }
                else
                    return todo
                })
        }
        case TODO_EDIT: {
            return state.map(todo => {
                if (todo._id === action._id) {
                    return {
                        ...todo,
                        isEditing: true,
                        isExpanded: true,
                    }
                }
                else return todo
                })
        }
        case TODO_ADD: {
            for (const post of state) {
                if (post._id === null) return state;
            }
            return [
                {
                    _id: null,
                    isEditing: true,
                    isExpanded: true,
                    title: action.title,
                    priority: 100,
                    description: "",
                    color: "",
                    state: "ACTIVE",
                    created_date: Date.now()
                },
                ...state
            ]
        }
        // case ADD_CONFIRM':{
        //     return state.map(post => {
        //         if (post._id === null)
        //             return Object.assign({}, post,
        //                 {
        //                     _id: action.data._id,
        //                     isEditing: false,
        //                     text: action.data.text,
        //                     created_date: new Date(action.data.created_date)
        //                 });
        //         else return post
        //         })
        // }
        
        case TODO_SAVE_SUCCESS: {
            return state.map(todo => {
                if (todo._id === action._id){
                    return {
                        ...todo,
                        _id: action.serverData._id,
                        isEditing: false,
                        description: action.serverData.description,
                        priority: action.serverData.priority,
                        color: action.serverData.color
                    }
                }
                else return todo
                })
        }
        case TODO_DELETE: {
            return state.filter(todo => {
                return todo._id !== action._id;
            })
        }

        default: return state || [];
    }
}




export const todoSave = (_id: ID, newData) => {
    if (_id === null)
        return (dispatch) => {
            const data = new FormData();
            data.append( "descbody", newData.body )
            data.append( "title", newData.title )
            data.append( "priority", newData.priority )
            data.append( "color", newData.color )
            const reqInit = { method: "POST", body: data }
            return fetch("/api/todos", reqInit)
                .then(response => {
                    if(response.ok) {
                        response.json().then(body => {
                            const serverData = body.data
                            dispatch(todoSaveSuccess(null, serverData))
                        })
                    } else {
                        dispatch(todoSaveFailed(_id))
                    }
                })
        }
    else
        return (dispatch) => {
            const data = new FormData();
            data.append( "descbody", newData.body )
            data.append( "title", newData.title )
            data.append( "priority", newData.priority )
            data.append( "color", newData.color )
            const reqInit = { method: "PUT", body: data }

            return fetch("/api/todos/"+_id, reqInit)
                .then(response => {
                    if(response.ok) {
                        response.json().then(json => {
                            const serverData = json.data
                            dispatch(todoSaveSuccess(_id, serverData))
                        })
                    } else {
                        dispatch(todoSaveFailed (_id))
                    }
                })
        }
}



export const todoDelete = (_id: ID) => ((dispatch) => {
    const data = new FormData();
    const reqInit = { method: "DELETE", body: data }

    return fetch("/api/todos/"+_id, reqInit)
        .then(response => {
            if(response.ok) {
                dispatch(todoDeleteClient(_id))
            } else {
                dispatch(todoSaveError(_id))
            }
        })
})
