import React from 'react';
import { connect } from 'react-redux';
import EditableTodo from './Todo';
import gql from "graphql-tag";
import { ApolloClient } from "apollo-client";
import { withApollo } from "react-apollo";

import { ITodo, ITodoState, todoAdd, todoExpand, todoEdit, todoSaveSuccess, todoSaveFailed } from './TodoActions'
import { Dispatch } from 'redux';
import * as API from '../../api'

interface ITodoListProps {
    todos: ITodoState
    onAddClick: (title: string) => void
}
interface ITodoListState {
    title: string
}

export class TodoList extends React.Component<ITodoListProps,ITodoListState> {
    private dispatch: Dispatch;
    private client: ApolloClient<any>;

    constructor(props) {
        super(props);
        this.dispatch = props.dispatch
        this.client = props.client
        this.state = {
            title: ""
        }

        this.handleAddTitleChange = this.handleAddTitleChange.bind(this);
        this.handleAddSubmit = this.handleAddSubmit.bind(this);
    }

    private handleAddSubmit(event) {
        event.preventDefault();
        this.setState({ title: "" })
        // if (this.state.title !== "")
        return this.props.onAddClick(this.state.title)
    }

    private handleAddTitleChange(event) {
        this.setState({ title: event.target.value })
    }


    public componentDidMount() {
        API.getTodos()
            .then(response => {
                this.dispatch({
                    type: 'TODO_INIT',
                    newState: response.data.todos,
                })
            })
            .catch(err => console.error(err))
    }

    public render() {
        const { todos } = this.props

        return (
            <div className="col-xs-12 col-sm-4" id="listtask_container">
                <form id="addform" onSubmit={this.handleAddSubmit}>
                    <div id="addbtn" onClick={this.handleAddSubmit}></div>
                    <input type="text" name="title" tabIndex={-1} onChange={this.handleAddTitleChange} value={this.state.title}></input>
                </form>
                {todos.map(todo =>
                    <div key={todo._id} >
                        <EditableTodo {...todo}/>
                    </div>
                )}
            </div>
        )
    }
}

// Connecting

const comparePriorities = (a: ITodo, b: ITodo): number => {
    const ap = a.priority
    const bp = b.priority
    if (ap > bp) {
        return -1;
    }
    if (ap < bp) {
        return 1;
    }
    return 0;
}

const mapStateToProps = (state, props) => {
    const sortedTodos = [...state.todos]
    sortedTodos.sort(comparePriorities)
    return {
        todos: sortedTodos
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        dispatch,

        onAddClick: (title) => {
            dispatch(todoAdd(title))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withApollo(TodoList))
