import React, { Component } from 'react';
import { connect } from 'react-redux';
import {Howl, Howler} from 'howler';


const initialState = {
    url: "/static/train.mp3",
    volume: 100,
    status: "STOPPED"
};

// let sound = new Howl({
//     src: [
//         '/static/MGS4Codec4.mp3',
//         '/static/DevotionAura.ogg'
//     ]
// });

const sounds = [
    new Howl({ src: '/static/DevotionAura.ogg', volume: 0.1 }),
    new Howl({ src: '/static/MGS4Codec4.mp3' }),
]


const playSound = (soundID) => {
    return {
        type: "SOUND_PLAY",
        soundID
    }
}


const soundReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'SOUND_PLAY': {
            sounds[action.soundID-1].play()
            return Object.assign({}, state, { status: "PLAYING" });
        }

        case 'SOUND_STOP': {
            return Object.assign({}, state, { status: "STOPPED" });
        }

        default:
            return state
    }
}




class SoundPlayer extends Component {
    play() {
        this.audioElement.play()
    }

    setVolume(volume) {
        this.audioElement.volume(volume)
    }

    render() {
        const { url, status } = this.props.sound
        const isPlaying = (status === "PLAYING")

        return <audio ref={(audio) => { this.audioElement = audio }} src={url} />   
    }
}


const mapStateToProps = (state, props) => {
    return {
        sound: state.sound
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        dispatch,

        // onAddClick: (title) => {
            // dispatch(todoAdd(title))
        // }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SoundPlayer)
export { soundReducer, playSound }
