import React from 'react';
import { Dispatch } from 'redux';
import { connect } from 'react-redux';
import Remarkable from 'remarkable';
import { GetGradientColor } from '../../util'
import { ClickableProgressBar } from '../ProgressBar/ProgressBar'
import {
    ITodoState,
    ITodo,
    todoAdd,
    todoExpand,
    todoEdit,
    todoSave,
    todoSaveSuccess,
    todoSaveFailed,
    todoDelete,
    todoDeleteClient
} from './TodoActions'
import "./Todo.scss"

const markdown = new Remarkable()


const MarkdownView = ({ source }) => {
    const mdhtml = { __html: markdown.render(source) }
    return (
        <div dangerouslySetInnerHTML={mdhtml}>
        </div>
    )
}


interface ITodoProps extends ITodo {
    dispatch: Dispatch

    onExpand: () => void
    onEditClick: () => void
    onEditSubmit: (newData: ITodoComponentState) => void
    onDelete: () => void
}
interface ITodoComponentState {
    title: string
    body: string
    priority: number
    color: string
    isHovering: boolean
}


// const Todo = ({ id, description, priority, title, onExpand, ...props }) => {
class Todo extends React.Component<ITodoProps, ITodoComponentState> {
    private dispatch: Dispatch;

    constructor(props) {
        super(props);
        this.dispatch = this.props.dispatch
        this.state = {
            title: props.title,
            body: props.description,
            priority: props.priority,
            color: props.color,
            isHovering: false
        };

        this.handleTitleChange = this.handleTitleChange.bind(this);
        this.handleBodyChange = this.handleBodyChange.bind(this);
        this.handleColorChange = this.handleColorChange.bind(this);
        this.handlePriorityChange = this.handlePriorityChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }

    private handleTitleChange(event) { this.setState({ title: event.target.value }) }
    private handleColorChange(event) { this.setState({ color: event.target.value }) }
    private handleBodyChange(event) { this.setState({ body: event.target.value }) }

    private handlePriorityChange(newPrio) {
        this.setState({ priority: newPrio })
    }

    private handleSubmit(event) {
        event.preventDefault();
        return this.props.onEditSubmit(this.state)
    }

    private handleMouseEnter(event) {
        this.setState({ isHovering: true })
    }
    private handleMouseLeave(event) {
        this.setState({ isHovering: false })
    }

    public render() {
        const { _id, description, priority, title } = this.props
        const { isExpanded, isEditing, color } = this.props
        const { onExpand, onEditClick, onDelete } = this.props

        const priorityColor = GetGradientColor(this.state.priority/100)
        // console.log("render todo", this.props)
        return (
            <section data-created="" styleName="todo" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>
                <form styleName="form" onSubmit={this.handleSubmit}>
                <div styleName="priority">
                    { this.state.isHovering && <div className="minus"></div> }
                    { this.state.isHovering && <div className="plus"></div> }
                    <span style={{ color: priorityColor }}>{ priority }</span>
                </div>
                {/* { this.props.state === "ACTIVE" ? (
                    <div className="status_active"></div>
                ) : (
                    <div className="status_complete"></div>
                )} */}
                <div styleName="title" onClick={ onExpand }>
                    { isEditing ? (
                        <input style={{color: '#'+color}} type="text" onChange={ this.handleTitleChange } name="title" value={this.state.title}></input>
                    ) : (
                        <span style={{color: '#'+color}}>{ title }</span>
                    )}
                </div>
                { (this.state.isHovering || isEditing) &&
                    <div styleName="buttons">
                        <div styleName="edit" onClick={onEditClick}></div>
                        <div styleName="complete"></div>
                        <div styleName="uncomplete"></div>
                        <div styleName="remove" onClick={onDelete}></div>
                    </div>
                }
                { isExpanded &&
                    <div styleName="description">
                        { isEditing ? (
                            <div styleName="desc_edit">
                                <textarea name="descbody" onChange={this.handleBodyChange} value={this.state.body}></textarea>
                                <div style={{ padding: "10px 15px 25px 0px" }}>
                                    <ClickableProgressBar onChange={ this.handlePriorityChange } color="#00aa00" value={this.state.priority}/>
                                </div>
                                <input className="color" type="text" name="color" onChange={ this.handleColorChange } value={this.state.color}></input>
                                <a styleName="save" onClick={this.handleSubmit} >Save</a>
                            </div>
                        ) : (
                            <div styleName="desc_view">
                                <MarkdownView source={ description }/>
                            </div>
                        )}
                    </div>
                }
                </form>
            </section>
        )
    }
}


const EditableTodo = (props) => {
    return (
        <Todo {...props} />
    )
}

// Connecting

const mapDispatchToProps = (dispatch, props) => {
    return {
        dispatch,
        onExpand: () => {
            dispatch(todoExpand(props._id))
        },
        onEditClick: () => {
            dispatch(todoEdit(props._id))
        },
        onEditSubmit: (newData) => {
            dispatch(todoSave(props._id, newData))
        },

        onDelete: () => {
            dispatch(todoDelete(props._id))
        },
    }
}

export default connect(null, mapDispatchToProps)(EditableTodo)

